.error-banner {
  z-index: 1;
  position: relative;
  text-align: center;
  transform: translate3d(0, 0, 0);
}
.error-banner .error-404 {
  margin: 112px auto 2em;
  padding-left: 160px;
  display: inline-block;
  height: 112px;
  background-position: top left;
  -webkit-animation: 0.5s ease-in-out fade-slide-left;
  animation: 0.5s ease-in-out fade-slide-left;
}
.error-banner .error-404, .error-banner .error-404:before, .error-banner .error-404:after {
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  content: "";
}
.error-banner .error-404:before, .error-banner .error-404:after {
  width: 160px;
  height: 112px;
}
