.title {
  width: 50vw;
  margin: auto;
  text-align: center;
}

@-webkit-keyframes fade-slide-left {
  0% {
    opacity: 0;
    transform: translate(28px, 15px);
  }
  50% {
    opacity: 0;
    transform: translate(28px, 15px);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes fade-slide-left {
  0% {
    opacity: 0;
    transform: translate(28px, 15px);
  }
  50% {
    opacity: 0;
    transform: translate(28px, 15px);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
